/* Style for the crossword table */
.crossword-table {
  margin: 20px auto;
  border-collapse: collapse;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

.crossword-table td {
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  text-align: center;
  padding: 0;
}

.crossword-table .active {
  background-color: #ffc107;
}

.crossword-table input {
  width: 100%;
  height: 100%;
  border: none;
  background: transparent;
  font-size: 16px;
  text-align: center;
}

.crossword-table input:focus {
  outline: none;
}

.crossword-table .highlight {
  background-color: #f2f2f2;
}

.crossword-table .cell-label {
  position: absolute;
  top: 3px;
  left: 2px;
  font-size: 9px;
  font-weight: bold;
  color: #888;
}
